
import React,{useState,createContext} from 'react';
import './index.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Container,Row,Col,Navbar,Nav,Form,FormControl,Button,Card,ListGroup,ListGroupItem} from 'react-bootstrap';

export default class Search extends React.Component{


constructor(props){
    super(props);
     this.state={
         search:"",
        loading:true,
        movie:null,
        searchcopy:"",
        totalpages:null,
        currentpage:null
    }

    this.handlechange=this.handlechange.bind(this)
    this.handlesubmit=this.handlesubmit.bind(this)
    this.nextpage=this.nextpage.bind(this)
    this.prevpage=this.prevpage.bind(this)

}

handlechange(e){
    e.preventDefault()
    this.setState({
        search:e.target.value
    })
    
}

async handlesubmit(e){
    e.preventDefault();
    
    const link='https://api.themoviedb.org/3/search/movie?api_key=d272326e467344029e68e3c4ff0b4059&language=en-US&query='+this.state.search

    const response=await fetch(link);
    const data= await response.json();
    console.log(data)
    this.setState({movie:data,loading:false,searchcopy:this.state.search,totalpages:data.total_pages,currentpage:data.page+1,search:""})
    console.log(this.state.totalpages,this.state.currentpage)
    
}
async nextpage(e){
    
    const link='https://api.themoviedb.org/3/search/movie?api_key=d272326e467344029e68e3c4ff0b4059&language=en-US&query='+this.state.search+'&page='+this.state.currentpage

    const response=await fetch(link);
    const data= await response.json();
    console.log(data)
    this.setState({movie:data,loading:false,searchcopy:this.state.search,currentpage:data.page+1})
    
    
}
   async prevpage(e){
    
    const link='https://api.themoviedb.org/3/search/movie?api_key=d272326e467344029e68e3c4ff0b4059&language=en-US&query='+this.state.search+'&page='+this.state.currentpage

    const response=await fetch(link);
    const data= await response.json();
    console.log(data)
    this.setState({movie:data,loading:false,searchcopy:this.state.search,currentpage:data.page-1})
    
    
}

render(){
    return(
        <div>
        <Container fluid style={{backgroundColor:'#7B8788',height:'fit-content',minHeight:'100vh'}}>
            <Row style={{justifyContent:"center",padding:'15px'}}>
                <Form>
                 <Form.Group controlId="formBasicEmail">
    
    <Form.Control type="text" placeholder="Search" onChange={this.handlechange} value={this.state.search} />
    
  </Form.Group>
  <Button variant="primary" type="submit" onClick={this.handlesubmit} style={{marginLeft: '30%'}}>
    search
  </Button>
  </Form>
                    
                
            </Row>
            <Row>
                
                
                    {this.state.loading?
                            ("")
                        :
                            (<><div className="search-result">search results for "{this.state.searchcopy}"</div>
                            <div className="review-cards">
               {this.state.movie.results.map(movie=>{
                                return(
                                        <div className="cards" key={movie.id}>
    
                                        <Card style={{ width: '18rem' }}>
                                            <Card.Img variant="top" src={movie.poster_path===null?'data:image/png;base64,':'http://image.tmdb.org/t/p/w185'+movie.poster_path} alt="no preview" />
                                            <Card.Body>
                                                <Card.Title>{movie.title}</Card.Title>
                                                <Card.Text>
                                                    {movie.overview.substring(0,100)}
                                                </Card.Text>
                                            </Card.Body>
                                            <ListGroup className="list-group-flush">
                                                <ListGroupItem><h6>release date:</h6><p>{movie.release_date}</p></ListGroupItem>
                                                <ListGroupItem><h6>language:</h6><p>{movie.original_language}</p></ListGroupItem>
                                                <ListGroupItem><h6>Rating:</h6><p>{movie.vote_average}</p></ListGroupItem>
                                            </ListGroup>
                                            
                                        </Card>
                                        </div>)})}</div></>)}
                                       
                    
                </Row>
                
            </Container>
            

                   </div> )
}

}